<template>
    <div v-if="show">
        <Head title="商品分类" :show="true" ></Head>
        <van-tabs v-model="activeName" title-active-color="#f44" swipeable>
            <van-tab v-for="(ts,i) in typeList" :key="i" :title="ts.text" :name="ts.value">
                <List 
                @changeallgoods="getNewGoodList"
                :goodtype="ts"
                :goodList="goodList"
                :goods="goodList.filter(item=>item.type.value==ts.value)"
                 />
            </van-tab>
        </van-tabs>
    </div>
</template>


<script>
import {mapState,mapActions,mapMutations } from "vuex";
export default {
    data(){
        return {
            // goodList:[],
            // typeList:[],
            show:false,
            activeName:0
        }
    },
    computed:{
        ...mapState(['goodList','typeList'])
    },
    methods:{
        getNewGoodList(){
            this.goodList.reverse();
        },
        ...mapMutations(['getGoodList']),
        ...mapActions(['getTypeListAsync'])
    },
    created(){
        if(!this.goodList.length){
            this.$axios.get("/vue/getGoods").then(res=>{
                // this.goodList  = res.data.result;
                this.getGoodList(res.data.result)
            })
        }
    
    },
    mounted(){
        if(!this.typeList.length){
            this.getTypeListAsync({url:"/vue/getGoodTypes",cb:()=>{
                this.show = true;
            }})
        }
        this.show = true;


        // this.$axios.get("/vue/getGoodTypes").then(res=>{
        //     this.typeList = res.data.result;
        //     this.show = true;
        // })
    }
}
</script>
